<template>
  <div class="send-message-container">
    
    <van-field
      v-model="message"
      rows="12"
      clearable
      type="textarea"
      maxlength="1000"
      :placeholder="`回复 ${userNick}`"
      show-word-limit
      :border="false"
    />

    <van-button
      @click="sendComment"
      class="send-btn"
      color="linear-gradient(90deg,rgba(246,197,67,1),rgba(239,140,56,1))"      text="发送"
      round
    />
    
  </div>
</template>

<script>
import Vue from 'vue';
import { Field, Button, Toast } from 'vant';
import { apiSaveComment } from '@/service/api/comment';

Vue.use(Field).use(Button).use(Toast);

export default {
  data() {
    return {
      message: '',
      userNick: '',
      messageId: null,
      messageUserId: null,
      srcCommentId: null
    }
  },

  created() {
    document.title = '发送评论';
  },

  mounted() {
    this.receiveQuery();
  },

  methods: {
    // todo 保存 query 参数到组件中
    receiveQuery() {
      this.userNick = decodeURIComponent( this.$route.query.userNick );
      this.messageId = this.$route.query.messageId;
      this.messageUserId = this.$route.query.messageUserId;
      this.message = decodeURIComponent( this.$route.query.content );
      this.srcCommentId = this.$route.query.srcCommentId;
    },
    
    // todo 发送评论
    async sendComment() {
      const saveCommentResponse = await apiSaveComment({
        msgId: this.messageId,
        msgUserId: this.messageUserId,
        content: this.message,
        srcCommentId: this.srcCommentId
      });
      if (!saveCommentResponse && saveCommentResponse.type === 'fail') return Toast('服务器错误，请联系客服');

      Toast( '发送成功' );
      this.message = '';
      this.$router.back();
    }
  }
}
</script>

<style lang="scss" scoped>
.send-message-container {
  width: 100%;
  height: 100%;
  background: rgba(246,246,246,1);

  .send-btn {
    width: calc(100% - 40px);
    margin: 20px 20px 0;
    height: 40px;
  }
}
</style>


